@media (min-width:320px)  { 
  /* smartphones, iPhone, portrait 480x320 phones */ 


}
@media (min-width:481px)  { 
  /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */ 
}
@media (min-width:641px)  { 
  /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */ 
}
@media (min-width:961px)  { 
  /* tablet, landscape iPad, lo-res laptops ands desktops */ 

}
@media (min-width:1025px) { 
  /* big landscape tablets, laptops, and desktops */ 
  img {
    max-width: 100%;
    max-height: 100%;
  }
  
  .gallery-parent-container {
    margin: 0 1em 0;
    display: grid;
    grid-template-columns: repeat(23, minmax(15%, 25%));
    grid-template-rows: repeat(3, minmax(250px, 1fr));
    grid-gap: 10px;
    border: 4px solid red;
    max-height: 1050px;
    overflow-x: scroll;
    overflow-y: hidden;
    .c1 {
      grid-column: 1/2;
      grid-row: 1/2;
    }
    .c2-r1 {
      grid-column: 2/3;
      grid-row: 1/2;
    }
    .c2-r2 {
      grid-column: 2/3;
      grid-row: 2/3;
    }
    .c3-r2 {
      grid-column: 3/4;
      grid-row: 2/3;
    }
    .c3-r3 {
      grid-column: 3/4;
      grid-row: 3/4;
    }
    .c4-r1 {
      grid-column: 4/5;
      grid-row: 1/2;
    }
    .c4-r2 {
      grid-column: 4/5;
      grid-row: 2/3;
    }
    .c5-r2 {
      grid-column: 5/6;
      grid-row: 2/3;
    }
    .c5-r3 {
      grid-column: 5/6;
      grid-row: 3/4;
    }
    .c6-r1 {
      grid-column: 6/7;
      grid-row: 1/2;
    }
    .c6-r2 {
      grid-column: 6/7;
      grid-row: 2/3;
    }
    .c7-r2 {
      grid-column: 7/8;
      grid-row: 2/3;
    }
    .c7-r3 {
      grid-column: 7/8;
      grid-row: 3/4;
    }
    .c8{
      grid-column: 8/9;
      grid-row: 1/2;
    }
    .c9-r1{
      grid-column: 9/10;
      grid-row: 1/2;
    }
    .c9-r2{
      grid-column: 9/10;
      grid-row: 2/3;
    }
    .c9-r3{
      grid-column: 9/10;
      grid-row: 3/4;
    }
    .c10-r1 {
      grid-column: 10/11;
      grid-row: 1/2;
    }
    .c10-r2 {
      grid-column: 10/11;
      grid-row: 2/3;
    }
    .c11-r2 {
      grid-column: 11/12;
      grid-row: 2/3;
    }
    .c11-r3 {
      grid-column: 11/12;
      grid-row: 3/4;
    }
    .c12-r1 {
      grid-column: 12/13;
      grid-row: 1/2;
    }
    .c12-r2 {
      grid-column: 12/13;
      grid-row: 2/3;
    }
    .c13-r2 {
      grid-column: 13/14;
      grid-row: 2/3;
    }
    .c13-r3 {
      grid-column: 13/14;
      grid-row: 3/4;
    }
    .c14 {
      grid-column: 14/15;
      grid-row: 1/2;
    }
    .c15-r2{
      grid-column: 15/16;
      grid-row: 2/3;
    }
    .c15-r3{
      grid-column: 15/16;
      grid-row: 3/4;
    }
    .c16-r1{
      grid-column: 16/17;
      grid-row: 1/2;
    }
    .c16-r2{
      grid-column: 16/17;
      grid-row: 2/3;
    }
    .c17-r2{
      grid-column: 17/18;
      grid-row: 2/3;
    }
    .c17-r3{
      grid-column: 17/18;
      grid-row: 3/4;
    }
  
    .c18-r2{
      grid-column: 18/19;
      grid-row: 2/3;
    }
    .c18-r3{
      grid-column: 18/19;
      grid-row: 3/4;
    }
  
    .c19-r1{
      grid-column: 19/20;
      grid-row: 1/2;
    }
    .c19-r2{
      grid-column: 19/20;
      grid-row: 2/3;
    }
    .c19-r3{
      grid-column: 19/20;
      grid-row: 3/4;
    }
    .c20-r2{
      grid-column: 20/21;
      grid-row: 2/3;
    }
    .c20-r3{
      grid-column: 20/21;
      grid-row: 3/4;
    }
    .c21-r1{
      grid-column: 21/22;
      grid-row: 1/2;
    }
    .c21-r2{
      grid-column: 21/22;
      grid-row: 2/3;
    }
    .c23 {
      grid-column: 23/24;
      grid-row: 1/2;
    }
  }

  
}
@media (min-width:1281px) { 
  /* hi-res laptops and desktops */ 

}








